import './slide'; //  jQuerytoggle
import './smoothscroll'; //  
import buttonGotoTop from './content/btn_gototop'; //  
import buttonHumburger from './content/btn_humburger'; //  
import accessibility from './content/accessibility'; //  
import buttonModal from './content/btn_modal'; //  
import partsHeader from './content/header'; //  
import displayLoader from './content/disp_loader'; //  
import contactForm from './content/contactform'; //  
import pageBackGround from './content/pagebackground'; //  
import osCheck from './content/oscheck'; //  OS
import adjustViewport from './adjustviewport'; //  
import myExternalLinks from './myexternallinks'; //  
import eeParallax from './eeparallax/eeparallax'; //  
import buttonCookie from './content/btn_cookie'; //  
import webpDetection from './webp_detection'; //  WebP

//  
import './gsap/eegsap';
//  wowup,upgroup
//  
import './gsap/eegsap_surface';
//  SCROLL
import './gsap/eegsap_scrollbutton';
//  
import './gsap/eegsap_parallax';
//  swiper
import './splide-setting';


// 
const bp = {
    sm: 640,
    md: 768,
    lg: 1024,
    xl: 1280
};

const btnHumburger = new buttonHumburger('.p-hamburger', ".p-spmenu__closebutton", ".l-header", ".p-spmenu");
const acsb = new accessibility();
const dispLoader = new displayLoader();
const contactform = new contactForm();
const pbg = new pageBackGround();
const oscheck = new osCheck();
const adjustviewport = new adjustViewport();
const eeparallax = new eeParallax();
const btncookie = new buttonCookie();


//----------------------------------------------------
//  
//----------------------------------------------------
const init = function() {
    //  
    btnHumburger.eventRegistration('.p-spmenu__inner', 'ul li a', '.l-header__buttonswrapper a');
    //  
    acsb.eventRegistration();
    //  
    btncookie.eventRegistration();

    //  
    dispLoader.init();
    if (wp_template == "front-page.php" ||
        wp_template == "home.php") {
        dispLoader.eventRegistration();
    }
    //  
    if (wp_template == "page-contact.php") {
        contactform.eventRegistration();
    }

    //  OS
    oscheck.markBody(); //  bodyOS(iOS Machintosh )

    //  
    adjustviewport.set();
};

//----------------------------------------------------
//   : 
//----------------------------------------------------
window.addEventListener('DOMContentLoaded', () => {
    init();
});

//----------------------------------------------------
//   : 
//----------------------------------------------------
window.addEventListener('scroll', () => {
});

//----------------------------------------------------
//   : 
//----------------------------------------------------
window.addEventListener("resize", () => {
    //  
    adjustviewport.task();
});

//----------------------------------------------------
//   : 
//----------------------------------------------------
window.addEventListener("keydown", (event) =>{
    //  
    acsb.task_key( event );

    //  
    if(event.key==="Escape") {
        btnHumburger.taskEsc();
    }
});
